<template>
  <v-layout>
    <template slot="necker">
      <banner :img="'/static/img/member_banner.jpg'"></banner>
      <ol class="breadcrumb">
        <li class="breadcrumb-item">
          <router-link :to="{path:'/'}">Home</router-link>
        </li>
        <li class="breadcrumb-item active">註冊</li>
      </ol>
    </template>
    <template slot="page-title">
      <h1>註冊</h1>
    </template>
    <template slot="body">
      <div class="container container-xs">
        <div class="login-box mr-auto mb-6">
          <btn-fb></btn-fb>
          <div class="text-center my-3">或</div>
          <div class="form">
            <div class="form-group"> <input type="text"
                class="form-control"
                :class="{'is-invalid':errors.has('account')}"
                name="account"
                v-model="form.account"
                v-validate="'required'"
                placeholder="用戶名稱"
                autofocus>
              <div v-show="errors.has('account')"
                class="invalid-feedback">{{ errors.first('account') }}</div>
            </div>
            <div class="form-group"> <input type="text"
                class="form-control"
                :class="{'is-invalid':errors.has('email')}"
                name="email"
                v-model="form.email"
                v-validate="'required|email'"
                placeholder="電子郵件"
                autofocus>
              <div v-show="errors.has('email')"
                class="invalid-feedback">{{ errors.first('email') }}</div>
            </div>
            <div class="row">
              <div class="col-6">
                <div class="form-group"> <input type="password"
                    v-model="form.password"
                    class="form-control"
                    :class="{'is-invalid':errors.has('password')}"
                    name="password"
                    v-validate="'required|min:6|confirmed:confirmPassword'"
                    placeholder="密碼"
                    autofocus>
                  <div class="invalid-feedback">{{ errors.first('password') }}</div>
                </div>
              </div>
              <div class="col-6">
                <div class="form-group"> <input type="password"
                    class="form-control"
                    :class="{'is-invalid':errors.has('confirmPassword')}"
                    name="confirmPassword"
                    v-validate="'required'"
                    placeholder="確認密碼"
                    autofocus>
                  <div class="invalid-feedback">{{ errors.first('confirmPassword') }}</div>
                </div>
              </div>
            </div>
            <div class="form-group"> <input type="text"
                class="form-control"
                :class="{'is-invalid':errors.has('phone')}"
                name="phone"
                v-model="form.phone"
                v-validate="'required|min:8'"
                placeholder="電話"
                autofocus>
              <div v-show="errors.has('phone')"
                class="invalid-feedback">{{ errors.first('phone') }}</div>
            </div>
            <div class="login-footer text-center mt-5 px-5">
              <div class="form-group ">
                <button type="submit"
                  @click.prevent="submit"
                  class="btn btn-block btn-primary">確認</button>
              </div>
              <div class="action mt-3">
                <router-link :to="{name:'forgot'}">忘記密碼?</router-link>
              </div>
            </div>
          </div>
        </div>
      </div>
    </template>
  </v-layout>
</template>

<script>
import VLayout from '@/layouts/Default';
import banner from '@/pages/_part/banner';
import btnFb from '@/components/btn-fb';
export default {
  name: 'account-index',
  inject: {
    $validator: '$validator'
  },
  components: {
    VLayout, banner, btnFb

  },
  data () {
    return {
      confirmPassword: '',
      form: {
        account: '',
        email: '',
        password: '',
        phone: '',

      }
    }
  },
  methods: {
    submit () {
      this.$confirm('目前功能製作中', '提示', {
        confirmButtonText: '確定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        this.$message({
          type: 'success',
          message: '成功!'
        });
      }).catch(() => {
        this.$message({
          type: 'info',
          message: '取消'
        });
      });
    }

  }
};
</script>



